      this.list = [...JSON.parse(localStorage.getItem(this.userName))];

    },
    search() {
      let nowList = [...JSON.parse(localStorage.getItem(this.userName))];

      for (let key in this.sform) {
        nowList = nowList.filter(res => {
          return res[key] == this.sform[key]
        })
      }
      this.list = nowList;
    },
    del(e) {
      let ndata = JSON.parse(localStorage.getItem(this.userName)).filter(res => {
        return res.id != e.id;
      })
      this.$confirm('确认删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        localStorage.setItem(this.userName, JSON.stringify(ndata));
        this.sform = {};
        this.list = [...JSON.parse(localStorage.getItem(this.userName))];
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消删除'
        });
      });
    },
    add() {
      this.form.id = `t${(new Date()).valueOf()}`;
      if (!this.form.by) {
        this.form.by = '否'
      }
      let data = [...JSON.parse(localStorage.getItem(this.userName))];
      data.unshift(this.form);
      localStorage.setItem(this.userName, JSON.stringify(data))
      this.list = [...JSON.parse(localStorage.getItem(this.userName))];
      this.sform = {};
      this.handleClose()
